.wrapper {
    background: #000a;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.warning {
    width: 800px;
    height: 400px;
    background: #fff;
    border-radius: 0.5em;
    padding: 1em;
    box-sizing: border-box;
}

.warning h2 {
    font-size: 3em;
    line-height: 1;
}

.warning button{
    font-size: 1.2em;
    border: 1px solid #333;
    padding: 0.5em 1em;
    background-color: #fff;
    border-radius: 0.25em;
    cursor: pointer;
}


@media all and (max-width: 600px) {
    .warning {
        width: 100%;
        height: 100%;
        border-radius: 0;
        padding-top: 4em;
    }
    
    .warning h2 {
        font-size: 2em;
    }
}