.ProfessionBarWrapper {
    margin: 2em 2em;
}

.ProfessionBarWrapper h2 {
    font-size: 3em;
    width: 50%;
}

.ProfessionBarWrapper p {
    font-size: 1.1em;
    width: 50%;
}


.ProfessionBar {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.ProfessionBarElementWrapper {
    display: flex;
    flex-direction: column;
    width: calc(90% /4);
    margin-right: calc(10% / 3);
    margin-bottom: 2em;
    text-decoration: none;
    color: #333;
}

.ProfessionBarElementWrapper:nth-child(4n) {
    margin-right: 0;
}

.ProfessionBarElement {
    width: 100%;
    border-radius: 0.5em;
    display: flex;
    transition: 0.3s;
    box-sizing: border-box;
    padding: 1em 0;
    color: #5577aa;
    cursor:default;
    font-size: 1.5em;
    font-weight: 700;
}


.ProfessionBarElement .leftSide {
    flex-direction: column;
    padding: 0.5em;
    flex-grow: 1;
    box-sizing: border-box;
}

.leftSide .typeTitle {
    font-size: 0.8em;
}

.leftSide .name {
    font-size: 1.3em;
    font-weight: 600;
    margin-top: 0.5em;
    height: 3em;
    margin-bottom: 0.5em;
}

.leftSide .timeLerning {
    font-size: 0.8em;
}

.rightSide {
    flex-grow: 1;
    width: 40%;
    height: auto;
}

.rightSide .icon {
    /* width: 100%;
    height: 100%; */
    width: auto;
    height: 100%;
    box-sizing: border-box;
    padding: 1em;
    background-size: cover;
}



.button {
    border: 1px solid #333;
    width: 240px;
    font-size: 1.1em;
    padding: 0.5em 1.5em;
    margin: 0 auto;
    border-radius: 0.25em;
    cursor: pointer;
}

.button:hover {
    background: #333;
    color: #fff;
}

@media all and (max-width: 600px) {
    .ProfessionBarWrapper {
        margin: 1em 1em;
    }
    
    .ProfessionBarWrapper h2 {
        font-size: 2em;
        width: 100%;
    }
    
    .ProfessionBarWrapper p {
        font-size: 1.1em;
        width: 100%;
    }
    .ProfessionBarElementWrapper {
        width: 40%;
    }

}