.Wpapper {
    padding:3em 3em;
    display: flex;
    background-color: #f3f3f3;
    margin: 5em 2em;
    border-radius: 1em;
}

.LeftSide {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.LeftSide ul {
    margin-left: 0;
    padding-left: 1.2em;
}

.LeftSide li {
    padding: 0.5em;
    font-size: 1.1em;
}

.Wpapper h1 {
    font-size: 3em;
    font-weight: 700;
}

.button {
    border: 1px solid #333;
    width: 240px;
    font-size: 1.1em;
    padding: 0.5em 1.5em;
    border-radius: 0.25em;
    cursor: pointer;
    transition: 0.5s;
}

.button:hover {
    background: #333;
    color: #fff;
}


.rightSide {
    flex-grow: 1;
}

.vuzTitle {
    font-size: 1.2em;
    margin-left: auto;
    text-align: right;
    font-weight: bold;
}

.vuzGirl {
    width: 300px;
    height: 300px;
    background-color: #fff;
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
    margin-left: auto;
    margin-top: 1em;
    border-radius: 500px;
    border: 1px solid rgb(217, 0, 122);
    transition: 0.3s;
}

.vuzGirl:hover {
    transform:rotate3d(1,1,1,45deg);
}

@media all and (max-width: 600px) {
    .LeftSide {
        width: 100%;
    }
    .Wpapper {
        display: flex;
        flex-direction: column-reverse;
    }
    .Wpapper h1 {
        width: 100%;
        font-size: 1.5em;
    }
    .rightSide {
        display: none;
    }
    .button {
        width: auto;
    }
}