.header {
    background-color: #fff;
    padding: 2em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1280px;
    margin: 0 auto;
    box-sizing: border-box;
}

.logo {
    font-size: 2em;
}

.allCoursesButton {
    border: 1px solid #333;
    border-radius: 100px;
    padding: 1em 2.4em;
    /* padding-right: 1.2em; */
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.allCoursesButton::after {
    content: 'â€º';
    transform: rotate(90deg);
    position: absolute;
    font-size: 1.5em;
    right: 0.7em;
    top: calc(50% - 0.6em);
}

.loginButton {
    font-size: 1.2em;
}



@media all and (max-width: 600px) {
    .header {
        padding: 0.5em;
        border-bottom: 1px solid #e1e1e1;
        width: 100%;
        box-shadow: 0 0 6px #3333;
        box-sizing: content-box;
        position: sticky;
        z-index: 9999;
        top: 0;
    }

    .allCoursesButton {
        padding: 0.5em 1em;
    }
    .allCoursesButton::after {
        display: none;
    }
}