.HeadersLinks {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.HeadLink {
    margin: 1em;
    font-size: 1.1em;
    text-decoration: none;
    color: #333;
    font-weight: 400;
}



@media all and (max-width: 600px) {
    .HeadLink {
        margin: 0.2em;
        font-size: 0.9em;
    }
    .HeadersLinks {
        display: none;
    }
}