.ButtonBlocksWrapper {
    margin: 2em 2em;
}

.ButtonBlocksWrapper p {
    font-size: 1.1em;
}

.ButtonsBlock {
    display: flex;
    flex-wrap: wrap;
}

.LandingButton {
    color: #333;
    font-weight: 700;
    text-decoration: none;
    font-size: 1.2em;
    border: 1px solid royalblue;
    padding: 0.5em 1em;
    border-radius: 50px;
    margin: 0.5em;
    margin-right: 0;
}

.LandingButton:first-child {
    margin-left: 0;
}


@media all and (max-width: 600px) {
    .LandingButton {
        margin: 0.2em 0;
    }
    
}
