.BigTitleWrapper {
    padding:3em 2em;
}

.BigTitleWrapper h1 {
    font-size: 4em;
    width: 60%;
    font-weight: 700;
}

@media all and (max-width: 600px) {
    .BigTitleWrapper {
        padding:1em 1em;
    }
    
    .BigTitleWrapper h1 {
        font-size: 2em;
        font-weight: 700;
    }
}