.ProfessionBarWrapper {
    margin: 2em 2em;
}

.ProfessionBarWrapper h2 {
    font-size: 3em;
    width: 50%;
}

.ProfessionBarWrapper p {
    font-size: 1.1em;
    width: 50%;
}


.ProfessionBar {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.ProfessionBarElementWrapper {
    display: flex;
    flex-direction: column;
    width: calc(96% /3);
    margin-right: calc(4% / 2);
    margin-bottom: 2em;
    text-decoration: none;
    color: #333;
}

.ProfessionBarElementWrapper:nth-child(3n) {
    margin-right: 0;
}

.ProfessionBarElement {
    width: 100%;
    background-color: #fff;
    border: 1px solid #333;
    border-radius: 0.5em;
    display: flex;
    transition: 0.3s;
    box-sizing: border-box;
    padding: 1em;
}

.ProfessionBarElement:hover {
    background: rgb(217, 0, 122);
    color:#fff;
    border: 1px solid transparent
}


.ProfessionBarElement .leftSide {
    flex-direction: column;
    padding: 0.5em;
    flex-grow: 1;
    box-sizing: border-box;
}

.leftSide .typeTitle {
    font-size: 0.8em;
}

.leftSide .name {
    font-size: 1.3em;
    font-weight: 600;
    margin-top: 0.5em;
    height: 3em;
    margin-bottom: 0.5em;
}

.leftSide .timeLerning {
    font-size: 0.8em;
}

.rightSide {
    flex-grow: 1;
    width: 40%;
    height: auto;
}

.rightSide .icon {
    /* width: 100%;
    height: 100%; */
    width: auto;
    height: 100%;
    box-sizing: border-box;
    padding: 1em;
    background-size: cover;
}



.button {
    border: 1px solid #333;
    width: 240px;
    font-size: 1.1em;
    padding: 0.5em 1.5em;
    margin: 0 auto;
    border-radius: 0.25em;
    cursor: pointer;
}

.button:hover {
    background: #333;
    color: #fff;
}

@media all and (max-width: 1200px) {
	.ProfessionBarElementWrapper {
        width: calc(96% /2);
        margin-right: calc(4% / 1);
    }

    .ProfessionBarElementWrapper:nth-child(3n) {
        margin-right: auto;
    }
    .ProfessionBarElementWrapper:nth-child(2n) {
        margin-right: 0;
    }
}


@media all and (max-width: 600px) {

    .ProfessionBarWrapper h2 {
        width: 100%;
        font-size: 2em;
    }

    .ProfessionBarWrapper p {
        width: 100%;
    }
	.ProfessionBarElementWrapper {
        width: calc(96% /1);
        margin-right: 0;
    }

    .ProfessionBarElementWrapper:nth-child(3n) {
        margin-right: 0;
    }
    .ProfessionBarElementWrapper:nth-child(2n) {
        margin-right: 0;
    }
}
