footer {
    background-color: #333;
    color: #fff;
    width: 100%;
    padding: 2em;
    padding-left: calc(50% - 640px);
    padding-right: calc(50% - 640px);
    box-sizing: border-box;
}

footer a {
    color: #e1e1e1;
    text-decoration: none;
    transition: 0.7s;
}


footer a:hover {
    color: #ccc;
}

.top_block {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.phone_number {
    font-size: 1.4em;
    font-weight: bold;
}

.address {
    padding: 1em 0;
}

.header_menu {
    font-size: 1em;
    font-weight: bold;
    padding-bottom: 0.8em;
}

.footer_links_block {
    display: flex;
    flex-direction: column;
}

.footer_links_block a {
    color: #fff;
    text-decoration: none;
    font-size: 1em;
    padding: 0.4em 0;
}

.bottom_block {
    margin-top: 2em;
    border-top: 1px solid #999;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.copyright {
    padding: 1em;
    font-size: 0.9em;
}

.bottom_links_block {
    display: flex;
    justify-content: space-between;
}

.bottom_links_block a {
    color: #fff;
    text-decoration: none;
    font-size: 1em;
    padding: 0.4em 2em;
}

@media all and (max-width: 900px) {
    .top_block {
        flex-wrap: wrap;
        justify-content: flex-start;
    }
    .top_block > div {
        width: calc(30% - 2em);
        margin: 1em;
    }
    .bottom_links_block {
        display: flex;
        flex-direction: column-reverse;
    }
}


@media all and (max-width: 600px) {
    .top_block {
        flex-wrap: wrap;
        justify-content: flex-start;
    }
    .top_block > div {
        width: calc(30% - 2em);
        margin: 1em;
    }
    .bottom_links_block {
        display: flex;
        flex-direction: column-reverse;
    }
}